
.profile{
    border-radius: 15px;
    background: #F7C244;
    border-color: #fbbc05; 
    color:#6647B6;
}

.profile .details{
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.profile .details span{
    font-size: 15px;
    font-weight: 600;
    overflow-wrap: break-word;
}

i.icon, i.icons {
    font-size: 1.4em;
}

.rewards{
    border-bottom-left-radius: 13px;
    border-bottom-right-radius: 13px;
    background: #6041B0;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    color:#fbbc05;
}

.contests-joined p,.prize-earned p,.current-rank p {
    font-size: 11.4px;
    color: #fff;
    font-weight: 600;
    text-align: left;
}


.btn-primary {
    font-weight: 600;
    border-color: #6647b6;
}
.wallet-details{
    font-weight: 600;
    border-radius: 15px;
    border-color: #fbbc05; 
    background: rgb(49, 54, 72);
    color:#ccc;
}

.ui.divider>.icon {
    color: #ccc;
}

.wallet-details .ui.toggle.checkbox .box:before, .ui.toggle.checkbox label:before{
    background: #ccc;
}




.ui.button {
    /* border-radius: 2em; */
    border: none;
    background: #ccc;
    color: '#313548';
    /* color: #6541B4; */
}

.modal {
    height:auto;
    top:auto;
    left:auto;
    bottom:auto;
    right:auto;
}


.account-options{
    -webkit-justify-content: space-between;
            justify-content: space-between;
}


.account .ui.header{
    color: #fff;
    font-size: 25px;
}

